.service-first {
  background-color: #eff1f2;
  padding-top: 80px;
  @media (max-width: 768px) {
    padding-top: 0;
  }
}
.service-first-item {
  display: flex;
  padding-top: 80px;
  align-items: center;
  gap: 50px;
  &__content {
    flex: 1;
    background-color: #fff;
    padding: 40px;
    h2 {
      font-size: 30px;
      text-transform: uppercase;
      font-weight: 700;
    }
    p {
      font-weight: 300;
      font-size: 14px;
      text-align: justify;
      margin: 10px 0 40px 0;
    }
    a {
      background-color: #db4537;
      color: #fff;
      padding: 10px 20px;
      letter-spacing: 6px;
      text-decoration: none;
      transition: all 0.3s;
      &:hover {
        background-color: #fec40f;
        color: #000;
      }
    }
  }
  &__image {
    flex: 1;
    position: relative;
    img {
      position: relative;
      margin: 50px 50px 50px 30px;
      object-fit: cover;
      z-index: 2;
      width: 100%;
      height: 400px;
    }
    &:before {
      content: "";
      position: absolute;
      width: 80%;
      height: 100%;
      background-color: #db4537;
      left: -2.5%;
    }
    &::after {
      content: "";
      position: absolute;
      width: 75%;
      height: 95%;
      background-color: #eff1f2;
      left: 0;
      top: 2.5%;
    }
  }
  @media (max-width: 768px) {
    flex-direction: column-reverse;
    gap: 20px;
    padding-top: 0;
    &__content {
      padding: 20px;
    }
    &__image {
      width: 100%;
      &::after,
      &::before {
        display: none;
      }
      img {
        margin: 0;
        height: 150px;
      }
    }
  }
}
