//About 1
.aboutBanner {
  padding-top: 80px;
  .banner__item {
    position: relative;
    img {
      height: 520px;
      width: 100%;
      object-fit: cover;
    }
    .item_content {
      position: absolute;
      background-color: rgba($color: #000000, $alpha: 0.6);
      color: #fff;
      padding: 10px 20px;
      left: 2%;
      bottom: 5%;
      font-size: 3.5rem;
    }
  }
  @media (max-width: 900px) {
    padding-top: 0;
  }
}

// About 2
.aboutSectionTwo {
  background-image: url(../images/bg-12.jpg);
  .item {
    align-items: stretch;
    .img-item {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .content-item {
      padding: 3rem 0 3rem 10rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      h3 {
        font-weight: 500;
        font-size: 2.5rem;
        margin-bottom: 2rem;
      }
      p {
        font-size: 1.6rem;
        font-weight: 400;
        margin-bottom: 1rem;
      }
    }
    &:nth-child(2n) {
      flex-direction: row-reverse;
      .content-item {
        padding: 3rem 10rem 3rem 0;
      }
    }
    @media (max-width: 568px) {
      .content-item {
        padding: 3rem 0;
      }
      &:nth-child(2n) {
        .content-item {
          padding: 3rem 0;
        }
      }
    }
  }
}

// About 3
.aboutMissionStatement__title {
  font-size: 3.6rem;
  @media (min-width: 768px) {
    font-size: 4.8rem;
  }
  @media (min-width: 1025px) {
    font-size: 6rem;
  }
  @media (min-width: 1200px) {
    font-size: 7.2rem;
  }
}
.aboutMissionStatement__subTitle {
  font-size: 1.7rem;
  font-weight: 500;
  @media (min-width: 1025px) {
    font-size: 2rem;
  }
}
.aboutMissionStatement__icons {
  display: flex;
  justify-content: center;
  margin-top: 6rem;
}
.aboutMissionStatementIcon {
  margin: 0 10px;
  @media (min-width: 768px) {
    margin: 0 15px;
  }
  @media (min-width: 1025px) {
    margin: 0 20px;
  }
  @media (min-width: 1200px) {
    margin: 0 25px;
  }
}
.aboutMissionStatementIcon__img {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #dfdfdf;
  border-radius: 10px;
}
.aboutMissionStatementIcon__content {
  margin-top: 20px;
  h4 {
    text-align: center;
    font-size: 1.3rem;
    font-weight: 500;
    color: var(--ink-50);
  }
}

// About 5
.aboutCustomer__containerCustom {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}
.aboutCustomer__img {
  -ms-flex: 0 0 40%;
  flex: 0 0 40%;
  max-width: 40%;
  padding: 0 15px;
  @media (max-width: 1024.98px) {
    display: none;
    order: 1;
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center;
    img {
      max-width: 300px;
    }
  }
}
.aboutCustomer__slider {
  -ms-flex: 0 0 60%;
  flex: 0 0 60%;
  max-width: 60%;
  padding: 0 15px;
  position: relative;
  .swiper-pagination-bullets.swiper-pagination--custom {
    position: static;
    margin-top: 3rem;
  }
  @media (max-width: 1024.98px) {
    order: 2;
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }
}
.aboutCustomerItem {
  position: relative;
  padding-left: 50px;
  padding-top: 50px;
  @media (max-width: 767.98px) {
    padding-left: 0;
    padding-top: 0;
  }
}
.aboutCustomerItem__img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
}
.aboutCustomerItem__avatarWrapper {
  width: 150px;
  height: 150px;
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  @media (max-width: 767.98px) {
    height: unset;
    position: static;
    margin-left: auto;
    margin-right: auto;
  }
}
.aboutCustomerItem__title {
  white-space: nowrap;
  color: var(--ink-50);
  font-weight: 400;
  font-size: 2rem;
  padding: 2rem 0;
  @media (min-width: 768px) {
    position: absolute;
    left: 100%;
    top: 0;
    padding: 0;
    padding-top: 10px;
    padding-left: 10px;
  }
}
.aboutCustomerItem__content {
  background-color: #efeded;
  position: relative;
  padding: 25px 25px 25px 165px;
  border-radius: 25px;
  p {
    font-size: 1.6rem;
    font-weight: 300;
    color: var(--ink-50);
  }
  &:before {
    content: "";
    background-image: url("../images/quote.svg");
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    width: 30px;
    height: 30px;
    left: 125px;
    top: 15px;
  }
  @media (max-width: 767.98px) {
    padding: 20px 20px 20px 60px;
    &:before {
      left: 20px;
      top: 15px;
    }
  }
}
