.projectDetailVideo__content {
  h3 {
    font-size: 1.8rem;
    color: var(--ink-70);
    font-weight: 500;
    margin-top: 15px;
  }
  p {
    margin-top: 15px;
    font-size: 1.6rem;
    font-weight: 700;
    color: var(--ink-70);
  }
  .row {
    margin-top: 5rem;
    .col {
      &:not(:first-child) {
        border-left: 1px solid #7b7979;
      }
    }
    p {
      color: var(--ink-50);
      text-align: center;
      ~ p {
        margin-top: 10px;
      }
      b {
        font-size: 2rem;
      }
      span {
        font-size: 1.6rem;
        font-weight: 300;
      }
    }
  }
}
.projectDetailVideo__img {
  width: 100% !important;
  a {
    @include img(calc(6 / 7));
  }
}
.projectDetailVideo__album {
  [class*="col-"] {
    margin-bottom: 30px;
  }
  .container {
    display: flex;
    justify-content: center;
    h2 {
      margin-bottom: 30px;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    padding: 0;
    li {
      height: auto;
      flex-grow: 1;
      padding: 3px;
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        vertical-align: bottom;
        border: 1px solid rgba($color: #fff, $alpha: 0.2);
      }
      &:last-child {
        flex-grow: 10;
      }
    }
  }
  @media (max-aspect-ratio: 1/1) {
    li {
      height: 30vh;
    }
  }
  @media (max-height: 480px) {
    li {
      height: 80vh;
    }
  }
  @media (max-aspect-ratio: 1/1) and (max-width: 480px) {
    ul {
      flex-direction: row;
    }

    li {
      height: auto;
      width: 100%;
    }
    img {
      width: 100%;
      max-height: 50vh;
      min-width: 0;
    }
  }
}
