.news {
  padding-top: 80px;
  @media (max-width: 1024px) {
    padding-top: 0;
  }
  .list-news {
    border-right: 1px solid rgba($color: #000000, $alpha: 0.1);
    padding-right: 30px;
    .newsItem {
      text-decoration: unset;
    }
    @media (max-width: 1024px) {
      border-right: unset;
      padding-right: 15px;
    }
  }
  .category {
    padding-left: 15px;
    @media (max-width: 1024px) {
      padding-left: 0;
    }
    h3 {
      background-color: #db4437;
      padding: 15px 0;
      border-top-right-radius: 10px;
      border-top-left-radius: 10px;
      font-size: 2rem;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
    }
    .list-categories {
      margin: 30px 0;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 40px;
        height: 4px;
        background-color: rgba($color: #000000, $alpha: 0.1);
        top: -20px;
      }
      .item {
        padding: 10px 0;
        border-bottom: 1px solid rgba($color: #000000, $alpha: 0.1);
        &:last-child {
          border-bottom: unset;
        }
        a {
          text-decoration: none;
          color: #334862;
          span {
            font-size: 16px;
            font-weight: 400;
            &:last-child {
              color: rgba($color: #000000, $alpha: 0.4);
              font-size: 14px;
              padding-left: 5px;
            }
          }
          &:hover {
            color: #000;
          }
        }
      }
    }
    &.newsest {
      .list-categories {
        .item {
          a {
            span {
              color: #334862;
              font-size: 14px;
              padding-left: 0;
            }
          }
        }
      }
    }
  }
}
