.clearfix:after {
  display: table;
  clear: both;
  content: "";
}
.d-none,
[hidden] {
  display: none !important;
}
.fw-300 {
  font-weight: 300;
}
.fw-400 {
  font-weight: 400;
}
.fw-500 {
  font-weight: 500;
}
.fw-600 {
  font-weight: 600;
}
.fw-700 {
  font-weight: 700;
}
.fw-800 {
  font-weight: 800;
}
.fw-900 {
  font-weight: 900;
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-justify {
  text-align: justify;
}
.text-last-center {
  text-align-last: center;
}
.text-underline {
  text-decoration: underline;
}
.text-no-underline {
  text-decoration: none;
}
.text-uppercase {
  text-transform: uppercase;
}
.text-capitalize {
  text-transform: capitalize;
}
.justify-content-center {
  justify-content: center;
}
.justify-content-end {
  justify-content: flex-end;
}
.justify-content-start {
  justify-content: flex-start;
}
.align-items-center {
  align-items: center;
}
.align-items-end {
  align-items: flex-end;
}
.align-items-start {
  align-items: flex-start;
}
.fullsize {
  width: 100%;
  height: 100%;
}
.object-cover {
  @extend .fullsize;
  object-fit: cover;
}

.overflow-hidden {
  overflow: hidden;
}

.object-contain {
  @extend .fullsize;
  object-fit: contain;
}
.blur-up {
  -webkit-filter: blur(5px);
  filter: blur(5px);
  transition: filter 400ms, -webkit-filter 400ms;
  &.lazyloaded {
    -webkit-filter: blur(0);
    filter: blur(0);
  }
}
.pt-section {
  padding-top: 30px;
  @media (min-width: 768px) {
    padding-top: 80px;
  }
}
.pb-section {
  padding-bottom: 30px;
  @media (min-width: 768px) {
    padding-bottom: 55px;
  }
  @media (min-width: 1200px) {
    padding-bottom: 80px;
  }
}
.pd-section {
  @extend .pt-section;
  @extend .pb-section;
}

.fs--18 {
  font-size: 1.8rem;
}

.fs--16 {
  font-size: 1.6rem;
}
