.form-control {
  height: 40px;
  border-radius: 5px;
  border: 1px solid #db4437;
  width: 100%;
  padding: 7px 15px;
  font-size: 1.5rem;
}
.form-button {
  button {
    font-size: 2rem;
    background-color: transparent;
    border: 1px solid #db4437;
    color: #db4437;
    padding: 5px 20px;
    &:hover {
      background-color: #db4437;
      color: #fff;
    }
  }
}

textarea.form-control {
  height: 160px;
  background-color: transparent;
}
input.form-control {
  background-color: transparent;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
