.project {
  padding-top: 100px;
  .section__blockTitle {
    margin: 50px 0 20px 0;
  }
  .project-title {
    h2 {
      background-color: unset;
      color: #db4537;
      text-align: center;
      width: auto;
    }
  }
  .project__container {
    .row {
      margin: 0;
    }
    .item {
      padding: 0;
      &:hover {
        .indexHotServiceItem__buttons {
          left: 30px;
          top: 30px;
          right: 30px;
          bottom: 30px;
          opacity: 1;
          visibility: visible;
        }
        .indexHotServiceItem__content {
          // opacity: 0.6;
          // visibility: hidden;
          transform: translateY(10px);
        }
        .indexHotServiceItem__img {
          background-color: #db4437;
          img {
            opacity: 0.65;
            transform: scale(1.1);
          }
        }
      }
    }
  }
  @media (max-width: 1024px) {
    padding-top: 0;
    .section__blockTitle {
      margin: 20px 0;
      h2 {
        font-size: 2rem;
      }
    }
  }
}
