.contact {
  background-image: url(../images/bg-12.jpg);
  padding-top: 80px;
  h2 {
    color: #db4437;
  }
  @media (max-width: 900px) {
    padding-top: 0;
  }
}
.contact__map {
  height: 350px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact__information {
  border: 2px dashed rgba($color: #db4437, $alpha: 1);
  border-radius: 10px;
  height: 100%;
  padding: 25px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0 100px;
  .information__item {
    display: flex;
    align-items: flex-start;
    &:first-child {
      justify-content: center;
      padding-bottom: 20px;
    }
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    > span {
      margin-top: 3px;
      color: #db4437;
      font-size: 2rem;
    }
    .right-item {
      margin-left: 10px;
      font-weight: 600;
      p {
        color: #000;
        font-size: 1.4rem;
        &:first-child {
          font-size: 1.6rem;
          color: #db4437;
        }
      }
    }
  }
  @media (max-width: 1024px) {
    margin: 0;
  }
}
